<template>
  <div>
    <v-header>
    </v-header>

    <v-content :type="'none'">
      <div class="cards" v-if="items.length > 0">
        <ul class="card-ul">
          <li v-for="item in items" class="card">
            <span class="img bank"></span>
            <div class="info">
              <p class="name font-16">{{item.bank}}</p>
              <p class="card-type font-12 opa-8">储蓄卡</p>
              <p class="bank-num">{{item.card}}</p>
            </div>
          </li>
        </ul>
      </div>
      <div class="none_bg no-card" v-else>
        <span class="img bg07" style="height:.71rem;top:42%;"></span>
        <p class="remark">还没有绑定银行卡，
          <a class="color-link" v-link="{name:'add-card',query:{from:'/wallet/bankCard',realName:realName}}">添加银行卡</a>
        </p>
      </div>
    </v-content>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import {toast} from '../../assets/js/toast'
  import {lt,url} from '../../const'
  import $ from 'jquery'

  export default{
    route:{
      data({from,to}){
        return this.$http.get(url.isRealName).then(
          function ({data:{success,data,resultMsg}}) {
            if (success) {
              if(data.isRealName){
                this.realName = data.realName;
                this.getList();
              }else{
                toast.alert('请先实名认证');
                window.location.href = "#/identify/real/step1?type=3";
                return;
              }
            } else {
              toast.alert(resultMsg);
            }
          }
        );
      }
    },
    ready(){
      $('.none_bg').css('height', $(window).height() * 0.9 + "px");
    },
    data(){
      return {
        realName:'',
        items:[]
      }
    },
    methods: {
      getList(){
        this.$http.get(url.bankCardList).then(
          function ({data:{success,data,resultMsg}}) {
            this.items = [];
            if (success) {
              this.items = data;
            } else {
              toast.alert(resultMsg);
            }
          }
        );
      }
    },
    components: {VHeader, VContent}
  }
</script>
<style scoped>
  .cards{
    padding: .1rem;
  }

  .card-ul{
    width: 100%;
  }

  .card-ul .card{
    width: 100%;
    margin-top: .1rem;
    background: -webkit-linear-gradient(left, #688cc5 , #4d78bb);
    background: -o-linear-gradient(right, #688cc5, #4d78bb);
    background: -moz-linear-gradient(right, #688cc5, #4d78bb); 
    background: linear-gradient(to right, #688cc5 , #4d78bb);   
    position: relative;  
    border-radius: .1rem;
  }

  .img.bank{
    position: absolute;
    left: .15rem;
    top: .15rem;
    width: .3rem;
    height: .3rem;
    background-size: .24rem;
    background-image: url(../../assets/img/wallet/bank.png);
    border: .03rem solid #c1d0e7;
    border-radius: 50%;
    background-color: #c1d0e7;
  }

  .card .info{
    padding-left: .55rem;
    padding-top: .15rem;
    padding-bottom: .15rem;
    color: #fff;
  }

  .info .bank-num{
    padding-top: .15rem;
    font-size: .2rem;
  }

  .img.bg07{
    background-image: url(../../assets/img/bg/null_icon07.png);
  }

</style>




